@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Creepster&family=Press+Start+2P&display=swap);
body {
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.game {
  width: 80vh;
  height: 100vh;

  margin: auto;

  text-align: center;
  font-family: "Bebas Neue", cursive;
  color: white;

  background-image: url(/memory_shuffle/static/media/background.dd372fdf.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.score-display {
  height: 33px;
  padding: 4px 16px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  font-family: "Press Start 2P", cursive;
  font-size: 19px;
  text-shadow: 1px 1px black;
  background-color: rgba(0, 0, 0, 0.6);
}

.new-game {
  height: 100%;

  display: flex;
  flex-direction: column;
}

.instructions {
  height: 3%;
  font-size: 25px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.defeat-screen,
.victory-screen {
  height: 25vh;

  padding: 35px;

  font-size: 53px;
  text-shadow: 3px 3px black;
}

.difficulty-select {
  height: 15%;
  width: 70%;
  margin: 10px auto;
  box-shadow: 10px 7px black;
  border: 1px solid black;

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 72px;
  text-shadow: 3px 3px #333;
}

.board {
  width: 80vh;

  display: grid;
  grid-gap: 3px;

  overflow: none;
}

.square {
  position: relative;
  box-sizing: border-box;

  border: 5px solid #444;
  border-radius: 15%;
}

.square::before {
  content: "";
  display: block;
  padding-top: 100%;
}

.square > img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

button {
  border: none;
  padding: 17px 22px;
  margin: 28px;

  font-family: inherit;
  font-size: 35px;
  text-shadow: 1px 1px black;
  color: white;
  box-shadow: 2px 2px black;
}

/* Colors */

body {
  background-color: black;
}

.game {
  background-color: #bfbfbf;
}

.score-display {
  color: #1ab2ff;
}

.how-to-play {
  color: #d9d9d9;
  margin-right: 12px;
}

.instructions {
  background-color: #737373;
}

button {
  background-color: #5ec783;
}

.difficulty-select:nth-child(2) {
  background-color: #5ec783;
}

.difficulty-select:nth-child(3) {
  background-color: #acc65d;
}

.difficulty-select:nth-child(4) {
  background-color: #c6c65d;
}

.difficulty-select:nth-child(5) {
  background-color: #c6915d;
}

.difficulty-select:nth-child(6) {
  background-color: #c65d5d;
  font-family: "Creepster", cursive;
}

.square {
  background-color: #f2f2f2;
}

/* Responsive Mobile Layout */

@media screen and (max-device-width: 768px) {
  .game {
    width: 100vw;
    height: calc(100vh - 44px);
  }

  .score-display {
    font-size: 15px;
    text-align: left;
    padding: 0;
  }
  
  .difficulty-select {
    height: 12%;
  }

  .board {
    width: 100vw;
    height: 100vw;

    margin-top: 30px;
  }
  
  .defeat-screen,
  .victory-screen {
    font-size: 44px;
  }

  button {
    padding: 10px;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
